.popup-delivery[v-cloak] .popup-delivery__row {
  visibility: hidden;
}
.popup-delivery__balloon__apply {
  margin-top: 20px;
}
.popup-delivery__caller {
  padding: 15px 40px;
  border-radius: 0.3rem;
  font-size: 14px;
  font-family: "PT Serif Caption";
  background: #7cbd41;
  cursor: pointer;
  color: #fff;
}
.popup-delivery__caller--desktop {
  margin-left: auto;
}
.popup-delivery__caller--mobile {
  display: none;
}
.popup-delivery__title-row {
  display: flex;
  margin-bottom: 2rem;
}
.popup-delivery__title-row__title {
  font-size: 2.6rem;
  font-family: "PT Serif Caption";
  padding: 0 0.5rem;
  color: #9b806f;
}
.popup-delivery__title-row__cafe-name {
  margin-left: auto;
  padding-left: 1em;
  font-size: 23px;
  color: #191919;
}
.popup-delivery__body {
  padding: 4rem 6rem;
  background: url(../images/popup-delivery/1.jpeg) 0 100% no-repeat;
  background-size: 47% auto;
}
.popup-delivery__pane {
  display: none;
}
.popup-delivery__pane.active {
  display: block;
}
.popup-delivery__row {
  display: flex;
  margin: 0 -25px;
}
.popup-delivery__col {
  flex: 0 0 auto;
  padding: 0 25px;
  min-height: 460px;
  width: 50%;
}
.popup-delivery__image {
  position: absolute;
  bottom: 0;
  left: 0;
}
.popup-delivery__tabs {
  padding: 10px;
  border-radius: 5px;
  background: #f4f4f4;
}
.popup-delivery__tabs__list {
  display: flex;
  margin: 0 -10px;
}
.popup-delivery__tabs__col {
  flex: 0 0 auto;
  width: 50%;
  padding: 0 10px;
}
.popup-delivery__tabs__item {
  transition: all 0.2s;
  display: block;
  font-size: 16px;
  padding: 0.9em;
  text-align: center;
  text-decoration: none !important;
  white-space: nowrap;
  color: #b5b5b5;
}
.popup-delivery__tabs__item::after {
  display: none;
}
.popup-delivery__tabs__item.active {
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  color: #7cbd41;
}
.popup-delivery__tabs__item:hover {
  color: #9c9c9c;
}
.popup-delivery__window {
  width: 95vw;
  max-width: 1100px;
}
.popup-delivery__input {
  position: relative;
  margin: 20px 0;
}
.popup-delivery__input__item {
  position: relative;
}
.popup-delivery__input__close {
  position: absolute;
  right: 1em;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
  background: #f3f3f3 url(../images/popup-delivery/close.svg) no-repeat 50% 50%;
  background-size: 50% 50%;
  width: 2em;
  height: 2em;
  font-size: 10px;
  cursor: pointer;
}
.popup-delivery__input .c-input {
  text-align: center;
}
.popup-delivery__submit {
  display: flex;
  justify-content: center;
}
.popup-delivery__submit .c-button {
  padding: 1em;
  text-transform: none;
  min-width: 140px;
  font-size: 14px;
}
.popup-delivery__map {
  height: 100%;
  background: #f3f3f3;
}
.popup-delivery__select {
  display: none;
  position: absolute;
  top: 100%;
  margin-top: 20px;
  background: #fff;
  overflow: auto;
  max-height: 300px;
  border-radius: 5px;
  width: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  z-index: 100;
}
.popup-delivery__select.active {
  display: block;
}
.popup-delivery__select div {
  border-top: 1px solid #f3f3f3;
  padding: 11px 10px;
  cursor: pointer;
}
.popup-delivery__select div:first-child {
  border-top: 0;
}
.popup-delivery__select div:hover {
  background: #fdfdfd;
}
.popup-delivery .ymaps-2-1-79-suggest-item {
  padding: 0;
  margin: 0 !important;
}
.popup-delivery .ymaps-2-1-79-search__suggest {
  padding: 0;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  margin-top: 20px;
  background: #fff;
  overflow: auto;
  max-height: 300px;
  border-radius: 5px;
  width: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1) !important;
  border: 0;
  z-index: 100;
}
.popup-delivery .ymaps-2-1-79-search__suggest-item {
  border-top: 1px solid #f3f3f3;
  padding: 11px 10px;
  font-size: 16px;
  font-family: "PT Serif Caption";
  cursor: pointer;
}
.popup-delivery .ymaps-2-1-79-search__suggest-item:first-child {
  border-top: 0;
}
.popup-delivery .ymaps-2-1-79-search__suggest-item:hover {
  background: #fdfdfd;
}

@media (max-width: 991.98px) {
  .popup-delivery__row {
    flex-wrap: wrap;
  }
  .popup-delivery__col {
    width: 100%;
    min-height: auto;
    margin-top: 4rem;
  }
  .popup-delivery__col:first-child {
    margin-top: 0;
  }
  .popup-delivery__map {
    height: 50vh;
  }
  .popup-delivery__caller {
    padding-right: 20px;
    padding-left: 20px;
  }
  .popup-delivery__caller--desktop {
    display: none;
  }
  .popup-delivery__caller--mobile {
    display: inline-block;
  }
  .popup-delivery__title-row {
    display: block;
    text-align: center;
    padding: 0 1.5rem;
  }
  .popup-delivery__title-row__cafe-name {
    padding-left: 0;
    font-size: 14px;
    line-height: 1.2857142857;
    margin: 7px 0 13px 0;
  }
}
@media (max-width: 729.98px) {
  .popup-delivery__caller--mobile {
    display: block;
    width: 100%;
  }
}